/**
 * @param name (String)
 * @param value (String)
 * @param isHeader (Bool)
 * @param odd (Number is index)
 * @returns JSX
 */
const RowLineTab = ({ values, isHeader, odd }) => {
  // console.log(values)
  const cls = !isHeader ? (odd % 2 ? 'odds' : 'even') : 't-header'
  return (
    <tr className={cls}>
      {Object.entries(values).map(([key, value]) =>
        isHeader ? (
          <ThRender key={key + odd} row={value} />
        ) : (
          <TdRender key={key + odd} header={key} row={value} id={values.id} />
        ),
      )}
    </tr>
  )
}

RowLineTab.propTypes = {
  name: PropTypes.string,
  values: PropTypes.array,
  isHeader: PropTypes.bool,
  odd: PropTypes.bool,
}

RowLineTab.defaultProps = {
  name: 'RowLine',
  values: [],
  isHeader: false,
  odd: false,
}
function capitalize(string) {
  if (typeof string !== 'string') {
    return ''
  }
  return string.charAt(0).toUpperCase() + string.slice(1)
}

function debounce(fn, wait) {
  let timeout
  return function (...args) {
    const later = () => {
      clearTimeout(timeout)
      // eslint-disable-next-line
      fn.apply(this, args)
    }
    clearTimeout(timeout)
    timeout = setTimeout(later, wait)
  }
}

function ThRender({ row }) {
  const [widthS, setWidthS] = useState(120)

  const refParent = React.useRef(null)
  // const isMove = React.useRef(null)
  const refResize = React.useRef(null)
  const refValue = React.useRef(0)

  const downHanler = (e1) => {
    let curCol = refParent.current
    let curColWidth = refParent.current ? refParent.current.offsetWidth : 50
    let pageX1 = e1.pageX

    refResize.current.ondragstart = function () {
      return false
    }

    function moveAt(pageX, pageY) {
      const delta = pageX - refParent.current.getBoundingClientRect().right
      refValue.current = refParent.current.getBoundingClientRect().width + delta

      refResize.current.style.right = -delta + 'px'
    }

    function onMouseMove(event) {
      if (curCol) {
        var diffX = event.pageX - pageX1
        refParent.current.style.width = curColWidth + diffX + 'px'
      }
    }

    // (3) перемещать по экрану
    document.addEventListener('mousemove', onMouseMove)

    // (4)  удалить более ненужные обработчики событий
    document.onmouseup = function (e) {
      refParent.current = undefined
      pageX1 = undefined
      curColWidth = undefined

      document.removeEventListener('mousemove', onMouseMove)
      document.onmouseup = null
    }
  }

  const ondragstart = function () {
    return false
  }
  return (
    <th
      key={row}
      ref={refParent}
      data-type="resizable"
      className={'row-block__header'}
      style={{ width: widthS }}
    >
      {row}
      <div
        ref={refResize}
        // draggable
        className="resable"
        onMouseDown={downHanler}
        ondragstart={ondragstart}
      />
    </th>
  )
}

function formatNull(arg) {
  console.log({ arg })
  let grossAmount = typeof arg === 'string' ? arg : String(arg)
  if (grossAmount.includes('.')) {
    grossAmount = grossAmount.split('.')
    if (grossAmount[1].length >= 2) {
      grossAmount = grossAmount[0] + '.' + grossAmount[1].slice(1)
    } else if (grossAmount[1].length === 1) {
      grossAmount = grossAmount[0] + '.' + grossAmount[1] + '0'
    } else if (grossAmount[1].length === 0) {
      grossAmount = grossAmount[0] + '.' + '00'
    }
  } else {
    grossAmount = grossAmount[0] + '.' + '00'
  }
  return grossAmount
}
