const editableCol = ['EAN',  'SenderPrdCode']
function TdRender({ header, row, id }) {
  const divOnChange = (e) => {
    const target = e.target.dataset.type
    const value = e.target.innerText
    // console.log('target', target, value)
  }
  return (
    <td key={row} className={'row-block__body'} data-type={header}>
      {header === 'Vat' ? (
        <SelectRender id={id} row={row} />
      ) : header === 'QTY' || header === 'Price1' ? (
        <InputRender id={id} row={row} header={header} />
      ) : editableCol.includes(header) ? (
        <div
          className="editing-div"
          data-type={header}
          contentEditable
          onInput={divOnChange}
        >
          {row}
        </div>
      ) : (
        row
      )}
    </td>
  )
}

function SelectRender({ row, id }) {
  const [rowValue, setRowValue] = useState(row)
  const editMode = useSelector((state) => state.desAdv.isEdit)

  const dispatch = useDispatch()

  const setRow = (e) => {
    const vat = e.target.value
    setRowValue(vat)
    console.log(id)
    console.log({ vat })
    // if (id === 2 || id === 0) {
    let newData = { id, vat }
    dispatch(switchTabLineSelect({ newData }))
    // }
  }
  React.useEffect(() => {
    let vat = row
    let newData = { id, vat }
    dispatch(switchTabLineSelect({ newData }))
  }, [row])

  return editMode ? (
    <select value={rowValue} onChange={setRow}>
      <option value="0">0</option>
      <option value="10">10</option>
      <option value="18">18</option>
      <option value="20">20</option>
    </select>
  ) : (
    <div>{row}</div>
  )
}

function InputRender({ id, header, row }) {
  const [rowValue, setRowValue] = useState(row)
  const [errorValue, setErrorValue] = useState(false)
  const dispatch = useDispatch()

  const refCurChar = useRef(0)
  const editMode = useSelector((state) => state.desAdv.isEdit)
  // console.log('header', header)

  const validateError = (value) => {
    if (
      (!validateQty(header, value) && header === 'QTY') ||
      (!validatePrice1(header, value) && header === 'Price1')
    ) {
      setErrorValue(true)
    } else {
      setErrorValue(false)
    }
  }
  React.useEffect(() => {
    validateError(row)
  }, [row])

  const setRow = (e) => {
    let value = e.target.value
    if (!refCurChar.current) {
      return false
    }
    console.log(value)
    // if (!chartValid.includes(value)) {
    //   return 0
    // }
    if (value === ',') {
      value = '.'
    }

    // console.log(id, header, value)
    // let newValue = value
    if (value.includes('.')) {
      // if (value.split('.')[1].length > 2 && header === 'Price1') {
      //   return
      // }
      // const newData = { id, header, value }

      if (!validateError(value)) {
        setRowValue(value)
      }
    } else {
      //let extendNum = header === 'QTY' ? '.000' : '.00'
      const nv = value //? value + extendNum : '1' + extendNum
      setRowValue(nv)
      validateError(nv)
      // const newData = { id, header, nv }
    }
    refCurChar.current = false
  }

  const setRowBlur = (e) => {
    let value = e.target.value
    let newData = { id, header, value }
    // console.log(newData)
    if (header === 'QTY') {
      dispatch(switchQty({ newData }))
      value = '-1' // for update line
      newData = { id, header, value }
      dispatch(setPrice({ newData }))
    } else if (header === 'Price1') {
      dispatch(setPrice({ newData }))
    }
  }

  const onKeyDownHandler = (e) => {
    // console.log(e.key)
    if (chartValid.includes(e.key)) {
      refCurChar.current = true
      setErrorValue(true)
      return false
    }
  }
  return (
    <input
      style={{
        border:
          errorValue && editMode ? '1px solid red' : '1px solid transparent',
      }}
      disabled={!editMode}
      value={rowValue}
      onChange={setRow}
      onKeyDown={onKeyDownHandler}
      onBlur={setRowBlur}
    />
  )
}

function validateQty(header, row) {
  let isNullOfCent = true
  let row2 = row
  // console.log(row)
  if (typeof row2 === 'string' && row2.includes('.')) {
    let num2 = row2.split('.')[1]
    isNullOfCent = num2 === '000'
  }
  var pattern = /^\d+.\d\d\d$/
  return header === 'QTY' && row <= 55 && pattern.test(row2) && isNullOfCent
}

function validatePrice1(header, row) {
  var pattern = /^\d+.\d\d$/
  // var pattern = /\w/gi
  return pattern.test(row) && row.includes('.') // returns a boolean
}

export default RowLineTab
