import React from 'react'
import { useSelector } from 'react-redux'
import LineBlock from '../../../lib/components/LineBlock'

import PropTypes from 'prop-types'

import './DesBlock.styles.scss'

const DesBlock = ({ id, type, mode }) => {
  let dataDesBlock = useSelector((state) => state.desAdv.OrderData)
  // console.log(dataDesBlock)
  if (type === 'order') {
    let newDataDesBlock = dataDesBlock.slice(0, 2)
    const gln = {
      name: 'GLN',
      label: 'GLN плательщика',
      value: '2000000009972',
      type: 'div',
      valueBold: false,
    }
    const deliveryDate = {
      name: 'DeliveryDate',
      label: 'Дата доставки',
      value: '2014-03-28',
      type: 'div',
      valueBold: false,
    }
    const rub = {
      name: 'code',
      label: 'Валюта',
      value: 'РУБ',
      type: 'div',
      valueBold: false,
    }
    newDataDesBlock.push(gln)
    newDataDesBlock.push(deliveryDate)
    newDataDesBlock.push(rub)
    dataDesBlock = newDataDesBlock
  }
  return (
    <div className="des-block">
      {dataDesBlock &&
        dataDesBlock.length &&
        dataDesBlock.map((data) => <LineBlock data={data} />)}
    </div>
  )
}

DesBlock.propTypes = {
  id: PropTypes.string,
  type: PropTypes.string,
  mode: PropTypes.string,
}

DesBlock.defaultProps = {
  id: 1232339,
  type: 'order',
  mode: 'read',
}
export default DesBlock
