import React, { useState } from 'react'
import { useSelector } from 'react-redux'

import PropTypes from 'prop-types'

import './DesBlockTable.styles.scss'
import LineBlock from '../../../lib/components/LineBlock'
import RowLineTab from './RowLineTab'

const DesBlockTable = React.memo(({ type }) => {
  const { dataDesBlockTable, tabSumData, dataOrderTable } = useSelector(
    (state) => state.desAdv,
  )
  let curData = { header: {}, data: [] }
  if (type === 'order') {
    curData = dataOrderTable
  } else if (type === 'desadv') {
    curData = dataDesBlockTable
  }
  // console.log('22', dataDesBlockTable.data)
  return (
    <div>
      <div className="tab-block">
        <table className="tcnt" cellspacing="4" border="1">
          <thead>
            {Object.keys(curData.header).length && (
              <RowLineTab isHeader values={Object.values(curData.header)} />
            )}
          </thead>
          <tbody>
            {curData.data.length &&
              curData.data.map((values, index) => (
                <RowLineTab
                  isHeader={false}
                  key={values.id + values.EAN}
                  values={values}
                  odd={index}
                />
              ))}
          </tbody>
        </table>
      </div>
      <div className="tab-summ">
        {tabSumData.map((line) => (
          <LineBlock data={line} />
        ))}
      </div>
    </div>
  )
})

