//     name: 'Номер заказа',
//     value: 'regress2506-2',
//     type: 'div',
//     valueBold: false,
//   },
// }

DesBlockTable.propTypes = {
  name: PropTypes.string,
  editMode: PropTypes.bool,
}

DesBlockTable.defaultProps = {
  name: 'DesAdvTab',
  editMode: false,
}

export default DesBlockTable
