import React, { useCallback, useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { switchShowModal } from '../../../store/slices/desAdv'

import PropTypes from 'prop-types'

import './Modal.styles.scss'
import LineBlock from '../LineBlock'
// import SelectRender from '../Select/Select'

/**
 * @param label (String)
 * @param data (Object){
    name: 'OrderNumber',
    label: 'Номер заказа',
    value: 'regress2506-2',
    type: 'div',
    valueBold: false,
  }
 * @returns JSX
 */
const Modal = React.memo(({ data }) => {
  const [visible, setVisible] = useState(false)
  // const [stateV, setstateV] = useState(data.value)
  // const { id, name, label, value, type, valueBold } = data
  const { showModal } = useSelector((state) => state.desAdv)
  const dispatch = useDispatch()
  useEffect(() => {
    setVisible(showModal)
  }, [showModal])

  const closeHandler = useCallback(() => {
    setVisible(false)
    dispatch(switchShowModal(false))
    // dispatch(setNDS({ id }))
  }, [data])

  const dataFile = {
    id: '5',
    name: 'DESADV',
    label: 'Из файла',
    value: '   ',
    type: 'file',
    valueBold: false,
  }

  return (
    visible && (
      <div className="bd-modal bd-des">
        <div className="modal" tabindex="-1" role="dialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h3 className="modal-title">Загруза накладной из XML</h3>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                  onClick={closeHandler}
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <LineBlock data={dataFile} />
                <p>Определять соответствие по колонке.</p>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={closeHandler}
                >
                  Save changes
                </button>
                <button
                  type="button"
                  className="btn btn-secondary"
                  data-dismiss="modal"
                  onClick={closeHandler}
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  )
})

Modal.propTypes = {
  label: PropTypes.string,
  data: PropTypes.object,
}

Modal.defaultProps = {
  name: 'Название(1)',
  data: {
    name: 'OrderNumber',
    label: 'Номер заказа',
    value: 'regress2506-2',
    type: 'div',
    valueBold: false,
    visible: false,
  },
}

export default Modal
