function appendData(jsonData, orderData) {
  // const orderData = OrderDataS.slice()
  let dictDesAdv = {}

  jsonData.forEach((el) => {
    if (el.value) {
      // console.log(el.name, '-', el.value)
      // console.log(el.value)
      dictDesAdv[el.name] = el.value
    } else {
      if (el.name === 'Options') {
        // console.log('name === Options', el.name)
        // console.log('Options', el.children[0].children)
        const options = iterableOptions(el.children[0].children)
        // console.log('options', options)
        dictDesAdv = { ...dictDesAdv, ...options }
        // console.log('dictDesAdv', dictDesAdv)
      } else if (el.name === 'DocDetail') {
        // const docDetail = getDocDetail(el.children)
        // dictDesAdv = { ...dictDesAdv, ...docDetail }
        // console.log('dictDesAdv', dictDesAdv)
      }
    }
  })
  // console.log('dictDesAdv', dictDesAdv)
  let data = []

  function iterableData(orderData) {
    orderData.forEach((el) => {
      const name = el ? el.name : ''
      if (dictDesAdv[name]) {
        // console.log('dictDesAdv[name', dictDesAdv[name])
        if (name.includes('Date')) {
          let newDate = dictDesAdv[name]
          // console.log('newDate', newDate)
          newDate = newDate
            ? newDate.slice(0, 4) +
              '-' +
              newDate.slice(4, 6) +
              '-' +
              newDate.slice(-2)
            : ' '
          // console.log('newDate', newDate)
          data.push({
            ...el,
            value: newDate,
          })
        } else {
          data.push({
            ...el,
            value: dictDesAdv[name],
          })
        }
      } else {
        console.log('ee', el)
      }
    })
    console.log('data3', data)
    return data
  }

  return iterableData(orderData)

  // console.log('orderData', orderData)
  // console.log('data', data)
}

function getDocDetail(jsonData) {
  const dictDocDetail = {}
  let orderData = jsonData.filter((el) => el.name === 'DocDetail')[0].children
  orderData.forEach((el) => {
    if (el.value) {
      dictDocDetail[el.name] = el.value
    } else if (el.name === 'Options') {
      el.children[0].children.forEach((e) => {
        dictDocDetail[e.children[0].value] = e.children[1].value
      })
    }
  })

  return {
    id: 2003,
    EAN: dictDocDetail['EAN'],
    ReceiverPrdCode: dictDocDetail['ReceiverPrdCode'],
    SenderPrdCode: dictDocDetail['SenderPrdCode'],
    ProductName: dictDocDetail['ProductName'],
    UOM: dictDocDetail['UOM'],
    OrderedQty: '12.000',
    Qty2: '12.000',
    QTY: dictDocDetail['QTY'],
    ItemsPerUnit: dictDocDetail['ItemsPerUnit'],
    Price1: dictDocDetail['Price1'],
    Vat: dictDocDetail['Vat'],
    Price2: dictDocDetail['Price2'],
    NetAmount: dictDocDetail['NetAmount'],
    TaxAmount: dictDocDetail['TaxAmount'],
    GrossAmount: dictDocDetail['GrossAmount'],
    OriginCountry: dictDocDetail['OriginCountry'],
    CustDeclNumber: '',
  }
}

function iterableOptions(orderData) {
  let data = []
  const dictDesAdvOptions = {}
  orderData.forEach((el) => {
    // const name = el ? el.name : ''
    // console.log('el', el.children[0].value, el.children[1].value)
    dictDesAdvOptions[el.children[0].value] = el.children[1].value
  })
  // console.log('dictDesAdvOptions', dictDesAdvOptions)
  return dictDesAdvOptions
}

