import React, { useCallback, useState, useRef } from 'react'
import { useDispatch } from 'react-redux'
import {
  setPais,
  setNDS,
  uploadFromFileXMLForTab,
  switchShowModal,
} from '../../../store/slices/desAdv'

import PropTypes from 'prop-types'

import './LineBlock.styles.scss'
import SelectRender from '../Select/Select'

/**
 * @param label (String)
 * @param data (Object){
    name: 'OrderNumber',
    label: 'Номер заказа',
    value: 'regress2506-2',
    type: 'div',
    valueBold: false,
  }
 * @returns JSX
 */
const LineBlock = React.memo(({ data }) => {
  const [stateV, setstateV] = useState(data.value)
  // const [selectedFile, setSelectedFile] = useState()

  const { id, name, label, value, type, valueBold } = data
  const fileUpload = useRef(null)
  const dispatch = useDispatch()

  const setPaisHandler = useCallback(() => {
    if (id === 3) {
      // console.log(id)
      dispatch(setPais({ id }))
    } else if (id === 1) {
      // console.log(id)
      dispatch(setNDS({ id }))
    } else if (id === 6) {
      dispatch(switchShowModal(true))
    }
  }, [data])

  const changeHandler = (event) => {
    event.preventDefault()
    const reader = new FileReader()
    reader.onload = async (e) => {
      const dataXML = e.target.result
      const { objSummData, tabData } = parseXML(dataXML)
      console.log('11', tabData)
      console.log('12', objSummData)
      dispatch(uploadFromFileXMLForTab({ objSummData, tabData }))

      dispatch(switchShowModal(false))
      // setSelectedFile(res)
    }
    reader.readAsText(event.target.files[0])
  }

  return (
    <div className="line-block" data-type={name}>
      {label && <div className="line-block_key">{label}:</div>}
      {type === 'div' && (
        <div className={valueBold ? 'bold' : 'line-block_value'}>{value}</div>
      )}
      {type === 'text' && (
        <input
          type="text"
          value={stateV}
          onChange={(e) => setstateV(e.target.value)}
        />
      )}
      {type === 'date' && (
        <input
          type="date"
          value={stateV}
          onChange={(e) => setstateV(e.target.value)}
        />
      )}
      {type === 'select' && <SelectRender data={data} />}
      {type === 'button' && (
        <input onClick={setPaisHandler} type="button" value={value} />
      )}
      {type === 'file' && (
        <input
          onChange={changeHandler}
          type="file"
          name="file"
          accept=".xml, .xaml"
          ref={fileUpload}
        />
      )}
    </div>
  )
})

LineBlock.propTypes = {
  label: PropTypes.string,
  data: PropTypes.object,
}

LineBlock.defaultProps = {
  name: 'Название(1)',
  data: {
    name: 'OrderNumber',
    label: 'Номер заказа',
    value: 'regress2506-2',
    type: 'div',
    valueBold: false,
  },
}

export default LineBlock

const DocTypeStatic = `DocType>
BuyerGLN>
SupplierGLN>
DeliveryPointGLN>
Format>
Currency>
DocumentNumber>
DocumentDate>
DeliveryDate>
OrderId>
OrderNumber>
OrderDate>`

const NameData = `EAN>
SenderPrdCode>
ReceiverPrdCode>
ProductName>
UOM>
OrderedQty>
QTY>
QTY2>
ItemsPerUnit>
Price1>
Price2>
Vat>
OriginCountry>`

function parseXML(dataXML) {
  const ignoreVal = ['Name', 'Value']
  let tmpObjDataForTab = {}
  let orderDate = {}
  const ArrData = []
  const objSummData = {}

  var result = dataXML.split('\n')
  let name = ''
  result.forEach((element) => {
    const k = element.split('<')[1].split('>')[0]
    const v = element.split('<')[1].split('>')[1]
    // console.log('ss', k, ' = ', v)
    if (k.includes(ignoreVal[0])) {
      // name
      name = k
    } else if (k.includes(ignoreVal[1])) {
      // value
      const value = v
      // console.log(name, value)
      objSummData[name] = value
    } else if (NameData.includes(k)) {
      // value

      tmpObjDataForTab[k] = v
      // console.log(el[0], el[1])
    } else if (k === '/DocDetail') {
      // ArrData
      ArrData.push(tmpObjDataForTab)
      tmpObjDataForTab = {}
      // console.log('DocDetail', k)
    } else if (DocTypeStatic.includes(k)) {
      // начала данных в таблицу
      orderDate[k] = v
    }
  })
  // console.log('orderDate', orderDate)
  // console.log('arrData', ArrData)
  const tabData = formatXmlJson(ArrData)

  return { objSummData, tabData }
}

function formatXmlJson(objData) {
  //DocDetail
  // console.log(objData)
  const allQTY = objData.reduce(
    (sum, el) => sum + parseFloat(el.QTY.split('.')[0]),
    0,
  )
  return objData.map((el, index) => ({
    id: index + 1,
    EAN: el['EAN'],
    ReceiverPrdCode: el['ReceiverPrdCode'],
    SenderPrdCode: el['SenderPrdCode'],
    ProductName: el['ProductName'] || 'ESSENCE ULTIME Бальзам д/поврежд.',
    UOM: el['UOM'],
    OrderedQty: el['OrderedQty'] || '12.000',
    Qty2: '12.000',
    QTY: el['QTY'] || '12.000',
    ItemsPerUnit: el['ItemsPerUnit'],
    Price1: el['Price1'],
    Vat: el['Vat'],
    Price2: el['Price2'],
    NetAmount: (
      parseFloat(el['Price1']) * parseFloat(el['QTY'].split('.')[0])
    ).toFixed(2),
    TaxAmount: el['TaxAmount'],
    GrossAmount: el['GrossAmount'],
    OriginCountry: el['OriginCountry'],
    CustDeclNumber: '',
  }))
}
